import React, { useEffect, useState } from 'react'
import { Typography, Button, Alert } from '@mui/material'
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import Backdrop from '@mui/material/Backdrop';
import Fade from '@mui/material/Fade';
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import LocalActivityIcon from '@mui/icons-material/LocalActivity';
import CircularProgress from '@mui/material/CircularProgress'
import Rating from '@mui/material/Rating';
import axios, { AxiosResponse } from 'axios';

class reserve {
  row: number;
  number: number;
  isReserve: boolean;
  isSelected: boolean

  constructor() {
    this.row = -1;
    this.number = -1;
    this.isReserve = false;
    this.isSelected = false;
  }
}

interface showtime {
  id: number,
  movie_id: number,
  date: string,
  start_time: string,
  end_time: string,
  available_seats: number,
  price: number
}

interface movie {
  id: number,
  title: string,
  description: string,
  genre: string,
  releaseYear: string,
  rating: number
  ratingCount: number,
  imageUrl: string
}

export default function CinemaHome() {
  // for total button
  const [btnArr, setBtnArr] = useState<reserve[]>([])

  // save number selected by a user
  const [numberSeat, setNumberSeat] = useState<number>(0)

  // save array of selected button
  const [seatSelectArr, setSeatSelectArr] = useState<number[]>([])

  const [openResult, setOpenResult] = useState<boolean>(false)
  const [successShop, setSuccessShop] = useState<boolean>(false)
  const [isOpenList, setIsOpenList] = useState<boolean>(false)

  //selected movie
  const [selectedShowtimeID, setSelectedShowtimeID] = useState<number>(-1)

  const [showtimes, setShowtimes] = useState<showtime[]>([])

  const [movieInfo, setMovieInfo] = useState<movie[]>([])



  // i & j for seats
  const rows: number = 5;
  const col: number = 8;
  const images: string[] = [process.env.PUBLIC_URL + '/shopping1.gif', process.env.PUBLIC_URL + '/shopping2.gif', process.env.PUBLIC_URL + '/shopping3.gif',
  process.env.PUBLIC_URL + '/shopping4.gif', process.env.PUBLIC_URL + '/Failed1.gif', process.env.PUBLIC_URL + '/Failed2.gif',]

  // initial btnArr
  useEffect(() => {
    const tempArr: reserve[] = [];
    for (let i: number = 0; i < rows; i++) {
      for (let j: number = 0; j < col; j++) {
        const temp: reserve = new reserve();
        temp.number = j;
        temp.row = i;
        tempArr.push(temp);
      }
    }
    setBtnArr(tempArr);
    getShowtimes()
    getMovies()

  }, [])

  const getShowtimes = async () => {
    const res: AxiosResponse<any, any, {}> = await axios.get("https://super-web-application-backend-production.up.railway.app/showtimes")
    setShowtimes(res.data)
    // alert(showtimes);
  }
  const getMovies = async (): Promise<void> => {
    const res: AxiosResponse<any, any, {}> = await axios.get(`https://super-web-application-backend-production.up.railway.app/movies`)
    setMovieInfo(res.data)
  }
  const selectMovie = (ID: number): void => {
    setIsOpenList(!isOpenList)
    setSelectedShowtimeID(ID)
  }
  const getMovieById = (ID: number): movie | undefined => {

    for (let index = 0; index < movieInfo.length; index++) {
      if (movieInfo[index].id === ID) {
        return movieInfo[index]
      }
    }

  }
  const ClickHandler = (row: number, number: number): void => {
    if (selectedShowtimeID < 0) {
      return alert("please select a showtime")
    }
    const tempArr: reserve[] = [...btnArr];
    var tempSeatArr: number[] = [...seatSelectArr]
    const index: number = (row * col) + number;
    if (!tempArr[index].isSelected) {
      setNumberSeat(numberSeat + 1);
      tempSeatArr.push(index + 1);
    } else {
      setNumberSeat(numberSeat - 1);
      tempSeatArr = tempSeatArr.filter((value: number) => value !== index + 1);
    }
    tempArr[index].isSelected = !tempArr[index].isSelected;
    setBtnArr(tempArr);
    setSeatSelectArr(tempSeatArr);
  }
  const BuyHandler = (): void => {
    setOpenResult(true)
    if (seatSelectArr.length === 0) {
      setSuccessShop(false);

      return;
    }
    const tempArr: reserve[] = [...btnArr]
    seatSelectArr.forEach((i) => {
      tempArr[i - 1].isReserve = true;
      tempArr[i - 1].isSelected = false;
    })
    setBtnArr(tempArr);
    setNumberSeat(0)
    setSuccessShop(true);
    setSeatSelectArr([])
  }




  return (
    <div className='bg-[#1c1c1c] w-full h-full flex flex-col items-center'>
      <Typography variant='h3' className='mt-28 mb-5 text-yellow-300'> Book Ticket </Typography>
      <div className='w-full md:w-3/4 lg:w-3/5 xl:w-1/2 flex flex-col items-center px-2'>
        <Typography className='bg-[#444] w-full text-center text-white rounded-md py-3'>Screen</Typography>
        <div className="relative w-full h-[50px] bg-black">
          <div className="absolute top-0 left-0 h-full w-full bg-[radial-gradient(ellipse_at_top,_rgba(255,255,255,.6),_transparent_80%)] animate-glow z-[1]"></div>
        </div>
      </div>

      <div className='w-full grid grid-cols-8 gap-x-1 gap-y-4 my-10 px-2 md:w-3/4 lg:w-3/5 xl:w-1/2  sm:gap-x-4 sm:gap-y-4'>
        {
          btnArr.map((btn) => {
            return (
              <>
                <Button variant="contained" key={(btn.row * col) + btn.number}
                  className={` ${btn.isSelected ? "bg-red-400" : btn.isReserve ? "bg-gray-500" : "bg-fuchsia-700"} 
                           ${btn.isSelected ? "hover:bg-red-500" : btn.isReserve ? "" : "hover:bg-fuchsia-800"}
                           transition duration-500 hidden sm:flex
                          `}
                  disabled={btn.isReserve}
                  onClick={() => ClickHandler(btn.row, btn.number)}>{(btn.row * col) + btn.number + 1}</Button>

                <button key={(btn.row * col) + btn.number}
                  className={` ${btn.isSelected ? "bg-red-400" : btn.isReserve ? "bg-gray-500" : "bg-fuchsia-700"} 
                           ${btn.isSelected ? "hover:bg-red-500" : btn.isReserve ? "" : "hover:bg-fuchsia-800"}
                           transition duration-500 flex justify-center size-11 text-white rounded-lg items-center sm:hidden 
                          `}
                  disabled={btn.isReserve}
                  onClick={() => ClickHandler(btn.row, btn.number)}>{(btn.row * col) + btn.number + 1}</button>

              </>
            )
          })
        }
      </div>

      <Box className='flex flex-col items-center w-full my-8'>
        <Box className='w-full flex justify-center'>
          <Button variant="text" className='flex justify-center gap-2 bg-[#ff9800] text-white hover:bg-[#e68900] ' onClick={BuyHandler}>
            <Typography variant="body1" >BUY</Typography>
            <AddShoppingCartIcon ></AddShoppingCartIcon>
          </Button>
        </Box>

        <Box className='flex w-full justify-center my-5'>
          <Button variant="text" onClick={() => setIsOpenList(!isOpenList)} className='flex justify-center gap-2 bg-[#607d8b] text-white hover:bg-[#455a64]' >
            <Typography variant="body1" > List of movies </Typography>
            <LocalActivityIcon ></LocalActivityIcon>
          </Button>
        </Box>
      </Box>

      <Modal
        open={isOpenList}
        onClose={() => { setIsOpenList(!isOpenList) }}
        closeAfterTransition
        slots={{ backdrop: Backdrop }}
        slotProps={{
          backdrop: {
            timeout: 500,
          },
        }}
      >
        <Fade in={isOpenList} >
          <Box className="w-full md:w-5/6 lg:w-2/3 xl:w-1/2 h-4/5 lg:h-[90%]  absolute top-1/2 left-1/2 px-2 bg-cyan-100 -translate-y-1/2 -translate-x-1/2  flex flex-col items-center  overflow-y-scroll " >
            {
              showtimes.length > 0 ?
                showtimes.map((showtime) => {
                  return (
                    <Box onClick={() => selectMovie(showtime.id)} className="flex  w-full h-3/4 sm:h-2/3 md:h-2/5 lg:h-1/3 xl:h-2/5   my-1 hover:bg-gray-200 px-2 py-1 border border-black rounded-md gap-y-3">
                      <Box className='flex flex-col h-full w-1/2 sm:w-1/3 md:w-1/4 xl:w-1/4 my-3 sm:my-0'>
                        <img src={getMovieById(showtime.movie_id)?.imageUrl} alt="" className='h-1/2 sm:h-full w-full object-cover rounded-md' />
                        <Box className="flex justify-center w-full h-1/2 sm:hidden rounded-md mt-1 mb-3">
                          <Box className=" relative flex-wrap flex-row justify-center w-full bg-cyan-300 rounded-md my-1 py-6">
                            <Box className="font-bold w-full text-center absolute top-0">
                              information
                            </Box>
                            <Box className="text-sm w-full text-center">
                              Available Seats : {showtime.available_seats}
                            </Box>
                            <Box className="text-sm w-full text-center">
                              Date : {showtime.date}
                            </Box>
                            <Box className="text-sm w-full text-center">
                              Start At : {showtime.start_time}
                            </Box>
                            <Box className="text-sm w-full text-center">
                              End At : {showtime.end_time}
                            </Box>
                            <Box className="text-sm w-full text-center">
                              Price : {showtime.price}$
                            </Box>
                            <Box className="flex justify-center">
                              <Rating name="read-only" value={getMovieById(showtime.movie_id)?.rating} precision={0.1} readOnly />
                            </Box>
                          </Box>
                        </Box>
                      </Box>

                      <Box className="flex flex-col items-center w-2/3 md:w-4/5 h-full">
                        <Box> <Typography className='text-sm font-bold' >{getMovieById(showtime.movie_id)?.title}</Typography></Box>
                        <Box className='flex flex-col justify-center md:flex-row w-full h-full'>
                          <Box className="w-full  md:w-2/3 bg-amber-300 rounded-md mx-1 my-1 flex flex-col items-center ">
                            <Box className="w-full "> <Typography variant="body2" className='px-5 py-2 text-justify  sm:text-sm' >{getMovieById(showtime.movie_id)?.description}</Typography></Box>
                            <Box className="flex flex-wrap w-full items-center justify-center ">
                              <Box> <Typography variant="body2" className='px-5 py-2 w-full sm:text-sm' >Genre : {getMovieById(showtime.movie_id)?.genre}</Typography></Box>
                              <Box> <Typography variant="body2" className='px-5 py-2 w-full sm:text-sm' >Realease Year : {getMovieById(showtime.movie_id)?.releaseYear}</Typography></Box>
                            </Box>
                          </Box>
                          <Box className="hidden justify-center w-full md:w-1/3 sm:flex rounded-md mx-1 ">
                            <Box className=" relative flex-wrap flex-row justify-center md:flex-col w-1/2 md:w-full bg-cyan-300 rounded-md mx-1 my-1">
                              <Box className="font-bold w-full text-center absolute top-0">
                                information
                              </Box>
                              <Box className="text-sm w-full text-center mt-7">
                                Available Seats : {showtime.available_seats}
                              </Box>
                              <Box className="text-sm w-full text-center">
                                Date : {showtime.date}
                              </Box>
                              <Box className="text-sm w-full text-center">
                                Start At : {showtime.start_time}
                              </Box>
                              <Box className="text-sm w-full text-center">
                                End At : {showtime.end_time}
                              </Box>
                              <Box className="text-sm w-full text-center">
                                Price : {showtime.price}$
                              </Box>
                              <Box>
                                <Rating name="read-only" value={getMovieById(showtime.movie_id)?.rating} precision={0.1} readOnly />
                              </Box>
                            </Box>
                          </Box>
                        </Box>
                      </Box>
                    </Box>
                  )
                })

                : <Box className="w-full h-full flex justify-center items-center"><CircularProgress></CircularProgress></Box>
            }
          </Box>
        </Fade>
      </Modal >

      <Modal
        open={openResult}
        onClose={() => { setOpenResult(!openResult) }}
        closeAfterTransition
        slots={{ backdrop: Backdrop }}
        slotProps={{
          backdrop: {
            timeout: 500,
          },
        }}

      >
        <Fade in={openResult}>
          <Box className={`${successShop ? 'bg-green-200' : 'bg-red-200'} w-11/12 md:w-3/5 lg:w-1/2 xl:w-1/3 h-3/5 md:h-1/2  absolute top-1/2 left-1/2 bg-gray-300 -translate-y-1/2 -translate-x-1/2 px-14 py-6 `} >
            <Box className=" flex justify-center">
              <img src={openResult ? (successShop ? images[Math.floor(Math.random() * 4)] : images[Math.floor(Math.random() * 2) + 4]) : ""} alt="" />
            </Box>
            <Alert className="my-2" color={successShop ? 'success' : 'error'} >
              {successShop ? "Your ticket purchase was successful!" : "Your purchase failed!"}
            </Alert>
            <Alert className="" color={successShop ? 'success' : 'error'}>
              {successShop ? "Enjoy your movie!" : "Please select at least a seat!"}
            </Alert>

          </Box>
        </Fade>
      </Modal>


    </div >

  )
}
